import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { TableData, ColEditDelete } from 'components/common';
import BtnAddNew from 'components/common/button/BtnAddNew';
import { Card, Row, Col, message } from 'antd';
import { getCertificateTypes,deleteCertificateType } from 'services/certificateType';

class ListAllCertificatesPage extends Component {

  state = {
    reload: 0,
  };

  handleDelete = async id => {
    await deleteCertificateType(id);
    message.success('Xóa thành công');
    this.setState({
      reload: this.state.reload + 1,
    });
  };

  render() {

    const columns = [
      {
        key: '1',
        title: 'Ký hiệu',
        dataIndex: 'id',
        align: 'center',
      },
      {
        key: '2',
        title: 'Tên chứng chỉ',
        dataIndex: 'label',
        align: 'center',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        align: 'center',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`#regulations/allcertificates/edit/${record.id}`}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];

    return (
      <Card title="Các loại chứng chỉ">
        <Row>
          <Col span={24}>
            <BtnAddNew href="#regulations/allcertificates/addNew" />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              service={getCertificateTypes}
              columns={columns}
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListAllCertificatesPage />
  </Page>
);



